import React, { Component } from "react";
import { withStyles, createMuiTheme } from "@material-ui/core/styles";
import {
  Divider,
  Grid,
  IconButton,
  TextField,
  Tooltip
} from "@material-ui/core";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import FormControlLabel from "@material-ui/core/FormControlLabel/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox/Checkbox";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select/Select";
import Input from "@material-ui/core/Input/Input";
import MenuItem from "@material-ui/core/MenuItem/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText/FormHelperText";
import { withRouter } from "react-router";
import Button from "@material-ui/core/Button/Button";

const theme = createMuiTheme();
const styles = theme => ({
  root: {
    width: "100%"
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: "33.33%",
    flexShrink: 0
  },
  appBar: {
    position: "relative",
    width: "100%",
    backgroundColor: "#3c9eb5"
  },
  toolbarTitle: {
    flex: 1
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary
  },
  button: {
    margin: theme.spacing(1)
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1)
  },
  input: {
    display: "none"
  }
});

// TODO: need an update button, and a "contract retrieved successfully", and "contract updated"
// default contract address should be the last one pulled from the registry.
// current contract version
class Settings extends Component {
  getEtherclearURL = versionName => {
    // Need to take the network ID out of the version name
    // TODO: repeated in App.js
    let name = this.props.extractVersionNumber(versionName);
    return `https://${name}-dot-default-dot-etherclear.appspot.com`;
  };

  versionLabel = (name, address) => {
    return name + " - " + address;
  };

  handleVersionChange = event => {
    this.setState({
      selectedVersion: {
        name: event.target.value.name,
        address: event.target.value.address
      },
      errorMessage: ""
    });
  };

  defaultVersion = () => {
    return {
      name: "N/A",
      address: "N/A"
    };
  };

  constructor(props) {
    super(props);
    this.state = {
      selectedVersion: this.defaultVersion(),
      fetchedVersions: false,
      errorMessage: undefined,
      gasPrice: this.props.gasPrice,
      updated: false
    };
  }

  changeGasPrice = event => {
    if (this.state.updated === true) {
      this.setState({ updated: false });
    }
    let amount = event.target.value;
    // Need to allow for empty string
    if (amount === "" || this.isValidAmount) {
      this.setState({ gasPrice: amount.toString() });
    }
  };

  isValidAmount = amount => {
    try {
      return parseInt(amount) > 0;
    } catch (e) {
      return false;
    }
  };

  updateGasPrice = event => {
    this.props.updateGasPrice(this.state.gasPrice);
    this.setState({ updated: true });
  };

  render() {
    // makes header mobile responsive
    const headerWidth = window.screen.width < 1200 ? "75%" : "22%";

    const headerSize = window.screen.width < 1200 ? "1.7em" : "2em";

    const dynamicFont = window.screen.width < 1200 ? ".8em" : "1.5em";

    // Update contract versions if we haven't already
    if (!this.state.fetchedVersions && this.props.contractVersions.length > 0) {
      // TODO: this causes a console error Cannot update during an existing state transition (such as within `render`)
      this.setState({
        selectedVersion: this.props.contractVersions[
          this.props.contractVersions.length - 1
        ],
        fetchedVersions: true
      });
    }

    // TODO: if contract list is undefined then disable button
    const disableContractButton =
      this.props.contractVersions.length === 0 ||
      this.props.network === undefined;

    const { classes } = this.props;
    return (
      <div style={{ padding: "40px" }}>
        <div style={{ width: headerWidth, margin: "auto" }}>
          <h3
            style={{
              fontSize: headerSize,
              textAlign: "center",
              fontWeight: "500",
              marginBottom: "0px"
            }}
          >
            Settings
          </h3>
          <Divider />
        </div>
        <div style={{ marginLeft: "24px" }}>
          <p style={{ fontWeight: "500", fontSize: "1.2em" }}>
            Links to archived builds
          </p>
          <div style={{ marginLeft: "24px" }}>
            <React.Fragment>
              <FormControl className={classes.formControl}>
                <Select
                  select={"true"}
                  value={this.state.selectedVersion}
                  renderValue={value => {
                    return this.versionLabel(value.name, value.address);
                  }}
                  fullWidth
                  onChange={this.handleVersionChange}
                  input={<Input name={"version"} id={"version-helper"} />}
                  disabled={!this.state.fetchedVersions}
                >
                  {this.props.contractVersions.map(key => {
                    // TODO: There could still be multiple versions that have the same information, not sure how to handle that.
                    // TODO: get selected background for the active menu item working here.
                    return (
                      <MenuItem
                        selected={
                          key.address === this.state.selectedVersion.address &&
                          key.name === this.state.selectedVersion.name
                        }
                        key={key.name}
                        value={key}
                      >
                        {this.versionLabel(key.name, key.address)}
                      </MenuItem>
                    );
                  })}
                </Select>
                <FormHelperText />
              </FormControl>
            </React.Fragment>
          </div>
          <Button
            className={classes.button}
            classes={{
              label: classes.buttonText
            }}
            variant="contained"
            href={this.getEtherclearURL(this.state.selectedVersion.name)}
            target={"_blank"}
            color="primary"
            disabled={disableContractButton}
            size="small"
          >
            Go to site
          </Button>
          <p>{this.state.errorMessage}</p>
          <p style={{ fontWeight: "500", fontSize: "1.2em" }}>
            Set default gas price
          </p>
          <p style={{ fontSize: "0.7em" }}>
            This will set the default gas price (in Gwei) used in all contract
            interactions. Only recommended for advanced users, or if
            transactions are taking too long. For recommended gas prices, please
            check <a href="https://ethgasstation.info/">ETH gas station</a>.
          </p>
          <div>
            <TextField
              id="outlined-multiline-static"
              className={classes.textField}
              onChange={this.changeGasPrice}
              type={"number"}
              style={{ width: "20%", marginLeft: "32px" }}
              value={this.state.gasPrice}
              disabled={false}
              margin="normal"
              label={"Gas price (Gwei)"}
            />
          </div>
          <div>
            <Button
              className={classes.button}
              classes={{
                label: classes.buttonText
              }}
              variant="contained"
              color="primary"
              disabled={!this.isValidAmount(this.state.gasPrice)}
              size="small"
              onClick={this.updateGasPrice}
            >
              Update
            </Button>
            {this.state.updated === true ? (
              <CheckCircleIcon style={{ color: "green" }} />
            ) : (
              ""
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(styles, { withTheme: theme })(Settings);
